* {
  box-sizing: border-box;
}
body{
  background: linear-gradient(rgba(79, 172, 201, 0.5), rgba(123, 174, 190, 0.7)),
    url(../images/pexels-lumn-167699.jpg);
  min-height: 100vh;
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: cursive, "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;

}
/* Header section start */

.container {
  text-align: center;
}
 .container h1 {
  margin: 0;
  margin-top: 1%;
  margin-left: -20%;
  mix-blend-mode: hard-light;
  color: rgb(46, 46, 66);
  font-weight: bolder;
  display: block;
}
.container i {
  margin-right: 0.5%;
}
.container input {
  margin-top: 2%;
  text-transform: capitalize;
  width: 20%;
  height: 35px;
  border: 1px solid black;
  background-color: transparent;
  box-shadow: 4px 4px 10px black;
  outline: none;
}
.container input:focus {
  outline-style: solid;
  outline-color: black;
}
.container input::placeholder {
  color: rgb(46, 46, 66);
  font-family: cursive;
}
/* Header section end*/

/* display section start */

.display-container p {
  text-align: center;
  font-size: 15px;
  margin-top: 2%;
  color: rgb(46, 46, 66);
}
.display-container p:hover {
  color: red;
  cursor: pointer;
}

.display-container .list{
  position: absolute;
  position: absolute;
  left: 50%;
  top: 48%;
  transform: translate(-50%,-50%);
}
.display-container ul {
  align-items: center;
  text-align: left;
  min-height: 200px;
  width: 400px;
  /* padding: 20px;  */
  position: relative;
  margin: 0;
  /* left: 37%; */
}
.display-container ul li {
  display: flex;
  text-transform: capitalize;
  justify-content: flex-end;
  flex-grow: 8;
  width: 90%;
  min-height: 2%;
  padding: 10px;
  list-style: none;
  border: 1px solid black;
  margin-bottom: 10px;
  box-shadow: 0 2px 10px black;
  border-radius: 2px;
  font-weight: bold;
  overflow: hidden;
}
.display-container ul li span {
  align-items: left;
  flex-grow: 100;
  padding: 0 10px;
  align-content: space-between;
}
.display-container ul li i {
  flex-grow: 0.5;
  align-items: right;
  margin: 2px;
}
.display-container ul li i:nth-child(3):hover {
  color: red;
  cursor: pointer;
}
.display-container ul li i:nth-child(2):hover {
  color: darkgreen;
  cursor: pointer;
}
/* display section end  */

/* MEDIA QUERIES START*/
@media screen and (max-width:900px) {
  .container input{
    width:30%
  }
  
}
@media screen and (max-width:768px){
  .container input{
    width:35%
  }
  .container h1{
    margin-top: 4%;
  }
  .display-container .list{
    top:52%
  }
}
@media screen and (max-width:600px){
  .container input{
    width:50%
  }
}
@media screen and (max-width:480px){
  .container input{
    margin-top: 4%;
  }
  .display-container .list{
    top:50%
  }
}
@media screen and (max-width:430px){
  .display-container .list{
    top:48%
  }
  .display-container ul{
    width:300px
  }
}
@media screen and (max-width:360px){
  .container h1{
    font-size: larger;
  }
  .container input{
    width: 65%;
  }
  
}

/* MEDIA QUERIES END  */