header {
  width: 100%;
  height: 50px;
  background-color: #305566;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .link {
  margin:0 50px;
  text-decoration: none;
  font-weight: bold;
  color: #579ab9;
  padding: 1%;
}
header .link:hover {
  background-color: rgba(87, 154, 185, 0.5);
  color: black;
}

header .link:focus {
  background-color: rgb(82, 162, 199);
  color: black;
}
header .link:active {
  color: white;
}

/* MEDIA QUERIES  */
@media screen and (max-width:480px){
  header{
    justify-content: space-evenly;
  }
  header .link{
    margin: 0 30px;
  }
}
@media screen and (max-width:320px){
  header{
    justify-content: space-evenly;
  }
  header .link{
    margin:0 10px;
   
    
  }
}

  
