.display {
  width: 600px;
  height: 400px;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}

/* MEDIA QUERIES  */
@media screen and (max-width: 1000px) {
  .display {
    width: 550px;
    top: 43%;
  }
}
@media screen and (max-width: 800px) {
  .display {
    width: 500px;
  }
}
@media screen and (max-width: 650px) {
  .display {
    width: 450px;
    font-size: 15px;
  }
}
@media screen and (max-width: 520px) {
  .display {
    width: 400px;
    font-size: 15px;
  }
}
@media screen and (max-width: 550px) {
  .display {
    width: 400px;
    font-size: 15px;
  }
}
@media screen and (max-width: 450px) {
  .display {
    width: 350px;
    font-size: 14px;
  }
}
@media screen and (max-width: 380px) {
  .display {
    width: 300px;
    font-size: 13px;
  }
}
